@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;&display=swap);
body {
  margin: 0;
}

.Name {
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size: 50px;
}

.Artist {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    font-size: 30px;
}

.Location {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    font-size: 20px;
}
.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    height: calc(100vh - 60px);
}

@media only screen and (max-width: 920px) {
    .Text_Container {
        width: 100% !important;
        height: calc(100vh - 60px) !important;
    }
    .Image_Container {
        width: 100% !important;
    }

    .Home_Container {
        flex-direction: column !important;
    }
}
.Footer_Content {
    width: 33.33%;
    height: 100%;
    display: flex;
    align-items: center;
    font-family: 'Rubik', sans-serif;
}

.Social_Media_Icon {
    width: 25px;
    -webkit-filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(184deg) brightness(108%) contrast(102%);
            filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(184deg) brightness(108%) contrast(102%);
    padding-right: 15px;
}
